.contest {
  @media (min-width: $tablet) {
    width: 100%;
    max-width: 660px;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (min-width: $desktop) {
    padding-left: 0;
    padding-right: 0;
    max-width: 940px;
  }
}

.contest-form {
  padding-top: 44px;
  padding-bottom: 16px;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet) {
    padding-top: 91px;
    padding-bottom: 48px;
  }

  @media (min-width: $desktop) {
    padding-bottom: 0;
    display: grid;
    grid-template-columns: 435px 435px;
    grid-column-gap: 70px;
    grid-row-gap: 50px;
  }
}

.contest-form__header {
  @include margin-padding-reset;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  color: $font-medium;

  @media (min-width: $tablet) {
    order: 1;
    padding-left: 0;
    padding-right: 0;
    font-size: 18px;
    width: 500px;
    display: inline-block;
    margin: 0 auto;
  }

  @media (min-width: $desktop) {
    width: 800px;
    grid-column: 1/-1;
  }
}

.contest-form__header span {
  display: none;

  @media (min-width: $tablet) {
    display: inline;
  }
}

.contest-form__submit {
  text-align: center;

  @media (min-width: $tablet) {
    margin-top: 13px;
    order: 7;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }

  @media (min-width: $desktop) {
    margin-top: 14px;
    grid-column: 1/-1;
  }
}

.contest-form__button {
  margin-bottom: 33px;

  @media (min-width: $tablet) {
    margin-bottom: 0;
  }
}

.contest-form__notice {
  @include margin-padding-reset;
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  color: $font-medium;
}

.contest-form__notice-star {
  color: $font-optional;
}

.contest-form__notice-text {
  display: none;

  @media (min-width: $desktop) {
    display: inline;
  }
}
